<template>
    <nav class="navbar navbar-expand-lg fixed-top bg-light py-3">
        <div class="container">
            <router-link to="/" class="navbar-brand" aria-current="page">
                <img src="../assets/img/logo1.png" alt="logo1">
            </router-link>
            <button class="navbar-toggler collapsed" type="button" data-bs-toggle="collapse"
                data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false"
                aria-label="Toggle navigation">
                <span class="navbar-toggler-icon top-bar"></span>
                <span class="navbar-toggler-icon bottom-bar"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <ul class="navbar-nav ms-3  ms-sm-auto mt-3 align-items-start align-items-sm-center">
                    <li class="nav-item">
                        <router-link to="/" class="nav-link " aria-current="page">Home</router-link>
                    </li>
                    <li class="nav-item">
                        <router-link to="/shop" class="nav-link " aria-current="page">Shop
                        </router-link>
                    </li>
                    <li class="nav-item">
                        <router-link to="/blog" class="nav-link " aria-current="page">Blog</router-link>
                    </li>
                    <li class="nav-item">
                        <router-link to="/about" class="nav-link " aria-current="page">About</router-link>
                    </li>
                    <li class="nav-item">
                        <router-link to="/contact-us" class="nav-link " aria-current="page">Contact Us
                        </router-link>
                    </li>
                    <li class="nav-item">
                        <router-link to="/cart" class="nav-link p-0" aria-current="page">
                            <i class="fa fa-shopping-cart position-relative">
                                <span :class="count_length == 0 ? 'd-none' : 'd-inline-block'" class=" badge">
                                    {{ count_length }}
                                </span>
                            </i>
                        </router-link>
                    </li>
                    <li class="nav-item">
                        <router-link to="/login" class="nav-link " aria-current="page" v-if="user == ''">
                            <button class="button_1 px-3" type="button">
                                <i class="fa fa-user me-2"></i>
                                Login
                            </button>
                        </router-link>
                        <div class="dropdown" v-else>
                            <button class="btn button_1 dropdown-toggle" type="button" data-bs-toggle="dropdown"
                                aria-expanded="false">
                                <div class="d-inline" v-if="!photo">
                                    <i class="fa fa-user me-2"></i> 
                                </div>
                                <div class="d-inline" v-else>
                                    <img class="userPhoto me-2" :src="photo" :alt="user">
                                </div>
                                <p class="d-inline mb-0">{{ user }}</p>
                            </button>
                            <ul class="dropdown-menu p-0" style="cursor: pointer;">
                                <li><a class="dropdown-item" @click="logout()">Log Out</a></li>
                            </ul>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
</template>
  
<script>
import { mapState } from 'pinia';
import { useProductStore } from '@/stores/ProductStore';
import { getAuth, signOut } from "firebase/auth";
const auth = getAuth();
export default {
    name: 'Navbar',
    data() {
        return {
            user: '',
            photo: ''
        }
    },
    methods: {
        userData() {
            const userData = JSON.parse(localStorage.getItem("Userdata"))
            if (userData) {
                this.user = userData.displayName
                this.photo = userData.photoURL
            } else {
                this.user = ''
            }
        }, logout() {
            localStorage.clear();
            signOut(auth).then(() => {
                this.$router.push({ path: '/login' })
            }).catch((error) => {
                console.log(error.code);
            });
        }
    },
    computed: {
        ...mapState(useProductStore, ['count_length']),
    },
    mounted() {
        this.userData()
    }
}
</script>
 